import "./index.scss"
import React, { Component } from "react"
import { Icon, Modal, Input, message } from "antd"
import HttpUtil from "Common/http-util"
import HttpApi from "Common/http-api"
import Utils from "../../common/utils"
import listerner from "../../assets/img/home/listerner.png";
import phone from "../../assets/img/home/phone.png";
export default class contactUs extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showContactUs: true,
      showLuckDrawFlag: false,
      couponList: [
        { name: "6折", per: "aa", img: "6" },
        { name: "8.5折", per: "ab", img: "85" },
        { name: "9折", per: "ac", img: "9" },
        { name: "9.5折", per: "ad", img: "95" },
        { name: "抽奖", img: "lijichoujiang" },
        { name: "9.5折", per: "ad", img: "95" },
        { name: "9折", per: "ac", img: "9" },
        { name: "8.5折", per: "ab", img: "85" },
        { name: "谢谢参与", per: "ae", img: "xiexiecanyu" },
      ],
      imgList: {
        aa: "https://www.webfunny.cn/resource/luckDraw/res-6.png",
        ab: "https://www.webfunny.cn/resource/luckDraw/res-85.png",
        ac: "https://www.webfunny.cn/resource/luckDraw/res-9.png",
        ad: "https://www.webfunny.cn/resource/luckDraw/res-95.png",
        ae: "https://www.webfunny.cn/resource/luckDraw/res-xiexie.png"
      },
      showLuckDrawRes: false,
      luckDrawResImage: "",
      selectKey: -1,
      finalKey: "ae",
      finalId: "",
      showEmailAndCompanyDialog: false,
      email: "",
      luckDrawClickAble: true,
      luckDrawShowPoint: JSON.stringify({
        "pointId": 7,
        "yong_hu_id": Utils.getWfCookie("monitorCustomerKey"),
        "ye_mian_biao_shi": window.location.pathname,
        "puGuangNeiRong": "抽奖转盘曝光"
      }),
      luckDrawCloseBtnFlag: false,
    }
    this.timer = null;
    this.luckDrawTimer1 = null;
    this.luckDrawTimer2 = null;
    this.luckDrawTimer3 = null;

    this.startSelect = this.startSelect.bind(this)
    this.mailboxValue = this.mailboxValue.bind(this)
  }
  componentDidMount() {
    this.getContactUsDom()

    /** 抽奖逻辑 开始 */
    // 首次进来一定弹框的逻辑
    // if (window.localStorage.showLuckDrawFirstTime !== "1") {
    //   this.setState({showLuckDrawFlag: true}, () => {
    //     window.localStorage.showLuckDrawFirstTime = "1"
    //   })
    // }
    // // 3秒钟后显示关闭按钮
    // setTimeout(() => {
    //   this.setState({luckDrawCloseBtnFlag: true})
    // }, 3000)
    /** 抽奖逻辑 结束 */
  }


  componentWillUnmount() {
    clearTimeout(this.timer)
    window.removeEventListener(this.getContactUsFun)
  }
  render() {
    const {showContactUs, showLuckDrawFlag, couponList, selectKey, showEmailAndCompanyDialog, email, luckDrawClickAble, showLuckDrawRes, luckDrawResImage, luckDrawCloseBtnFlag} = this.state;
    return <div className="contact-us-container">
      { false && <div className="luck-draw" onClick={this.showLuckDraw.bind(this)}/> }
      {
        showLuckDrawFlag &&
        <div className="luck-draw-box">
          <div className="draw-box" onClick={(e) => { e.stopPropagation() }} _webfunny-eo={this.state.luckDrawShowPoint}>
            {
              luckDrawCloseBtnFlag === true &&
              <Icon className="close-btn" type="close-circle" onClick={() => {
                this.setState({showLuckDrawFlag: false})
                window.webfunnyEvent(5).trackEvent({
                  yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
                  dian_ji_xing_wei: "关闭抽奖界面"
                })
              }}/>
            }
            <div className="list-box">
              {
                couponList.map((item, index) => {
                  if (index === 4) {
                    return <div key={index} className={`draw-item-start  ${ luckDrawClickAble === false ? "draw-item-disable" : ""}`} onClick={this.inputEmailAndCompany.bind(this)}>
                      <img style={{width: 50}} src={require(`Images/contactUs/${item.img}.jpeg`)} />
                    </div>
                  }
                  return <div key={index} className={`draw-item ${ selectKey === index ? "draw-item-sel" : ""}`}>
                    <img src={require(`Images/contactUs/${item.img}.jpeg`)} />
                    {
                      index !== 8 &&
                      <span>{ item.name }</span>
                    }
                  </div>
                })
              }
            </div>
          </div>
          {
            showLuckDrawRes === true &&
            <div className="luck-draw-res" onClick={(e) => { e.stopPropagation() }}>
              <img src={luckDrawResImage} onClick={(e) => {
                e.stopPropagation();
                this.setState({showLuckDrawRes: false})
                window.webfunnyEvent(5).trackEvent({
                  yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
                  dian_ji_xing_wei: "我知道了"
                })
              }}/>
            </div>
          }
        </div>
      }
      {
        showContactUs && <div className="reach-me"
          onMouseEnter={() => {
              window.webfunnyEvent(5).trackEvent({
              yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
              dian_ji_xing_wei: "联系我们"
            })
          }}
        >
          <img src={listerner}/>
          <span className="reach-our">购买咨询</span>
          <div className="buy-qr-code">
            <p className="title">
              <span><b>企业试用</b>，可联系我们<br/>直接建群</span>
            </p>
            {/* <img className="qr-code-img" src={`//webfunny.cn/resource/qr-code2.png?t=${Math.random()}`}/> */}
            <img className="qr-code-img" src={"https://www.walkingfunny.com/_next/image?url=%2F_next%2Fstatic%2Fmedia%2Fqr-code2.2065d361.png&w=256&q=75"}/>
            <div className="call">
              <img src={phone} alt="" />
              {/* 189 3095 0656 */}
              0519-88559985
            </div>
          </div>
        </div>
      }
      {
        showContactUs && <div className="wxq-box"
          onMouseEnter={() => {
            window.webfunnyEvent(5).trackEvent({
              yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
              dian_ji_xing_wei: "交流群2"
            })
          }}
        >
          <Icon type="message" />
          <span className="reach-our">技术群</span>
          {/* <div className="qr-code">
            <p className="title"><span>欢迎扫码进<b>1</b>群沟通</span></p>
            <img src={`https://webfunny.cn/resource/wxq.png?r=${Math.random()}`}/>
          </div> */}
          <div className="qr-code2">
            <p className="title"><span>欢迎扫码进<b>2</b>群沟通</span></p>
            <img src={`//www.webfunny.cn/resource/wf_wxq2.png?r=${Math.random()}`}/>
          </div>
        </div>
      }
      <Modal
        title="请填写优惠券接收邮箱"
        centered
        visible={showEmailAndCompanyDialog}
        onCancel={() => {
          this.setState({showEmailAndCompanyDialog: false});
          window.webfunnyEvent(5).trackEvent({
            yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
            dian_ji_xing_wei: "取消抽奖"
          })
        }}
        onOk={() => {
          window.webfunnyEvent(5).trackEvent({
            yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
            dian_ji_xing_wei: "开始抽奖"
          })
          const reg = /^[a-zA-Z0-9._-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
          if (!reg.test(this.state.email)) {
            message.error("请输入正确的邮箱地址！")
            console.error("抽奖输入错误邮箱")
            return
          }
          this.setState({showEmailAndCompanyDialog: false}, () => {
            HttpUtil.post(HttpApi.getCouponInfo, {email: this.state.email}).then((res) => {
              if (res.data === 1) {
                message.error(res.msg)
              } else {
                const { discount, id } = res.data
                const finalKey = Utils.b64DecodeUnicode(discount)
                this.setState({ finalKey, finalId: id }, () => {
                  this.startSelect()
                })
              }
            })
          })
        }}
        okText="开始抽奖"
        cancelText="取消"
        width={568}
        className="web-funny-mailbox"
      >
        <p>请填写有效的邮箱地址，每个邮箱仅限一次抽奖机会哦！</p>
        <p className="modal-text"><span>您的邮箱：</span>
          <Input
            className="email-input"
            // addonAfter={<Icon component={Email}/>}
            placeholder="将发送到您的邮箱，请填写正确的邮箱地址"
            value={email}
            onChange={(e) => {
              this.mailboxValue(e.target.value)
            }}
          />
        </p>
      </Modal>
    </div>
  }
  mailboxValue(email) {
    this.setState({email})
  }
  inputEmailAndCompany() {
    const { luckDrawClickAble } = this.state
    if (luckDrawClickAble === false) return
    this.setState({showEmailAndCompanyDialog: true})
    window.webfunnyEvent(5).trackEvent({
      yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
      dian_ji_xing_wei: "立即抽奖"
    })
  }
  startSelect() {
    this.setState({luckDrawClickAble: false})
    const { couponList, finalKey, finalId } = this.state
    const indexArr = [0, 1, 2, 5, 8, 7, 6, 3]
    let tempIndex = 0
    this.luckDrawTimer1 = setInterval(() => {
      tempIndex ++
      if (tempIndex > 7) {
        tempIndex = 0
      }
      this.setState({selectKey: indexArr[tempIndex]})
    }, 200)

    setTimeout(() => {
      clearInterval(this.luckDrawTimer1)
      this.luckDrawTimer2 = setInterval(() => {
        tempIndex ++
        if (tempIndex > 7) {
          tempIndex = 0
        }
        this.setState({selectKey: indexArr[tempIndex]})
      }, 100)
    }, 2000)

    setTimeout(() => {
      clearInterval(this.luckDrawTimer2)
      this.luckDrawTimer3 = setInterval(() => {
        tempIndex ++
        if (tempIndex > 7) {
          tempIndex = 0
        }
        const finalIndex = indexArr[tempIndex]
        this.setState({selectKey: finalIndex})
        if (couponList[finalIndex].per === finalKey) {
          clearInterval(this.luckDrawTimer3)
        }
      }, 300)
    }, 5000)
    setTimeout(() => {
      HttpUtil.post(HttpApi.sendCouponEmail, {id: finalId}).then(() => {
        this.setState({
          luckDrawClickAble: true,
          showLuckDrawRes: true,
          luckDrawResImage: this.state.imgList[finalKey]
        })
      }).catch(() => {
        message.error("网络异常，请稍后再试！")
      })
    }, 6500)
  }
  showLuckDraw() {
    this.setState({showLuckDrawFlag: true})
    window.webfunnyEvent(5).trackEvent({
      yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
      dian_ji_xing_wei: "1111抽奖"
    })
  }
  getContactUsDom = () => {
    const contactUsDom = document.getElementsByClassName("o_livechat_button")[0]//获取联系我们container
    if (!contactUsDom) {
      this.timer = setTimeout(() => {
        this.getContactUsDom()
      })
      return
    }
    this.setState({
      showContactUs: false
    })
    contactUsDom.addEventListener("click", this.getContactUsFun)

    const liveChatDom = document.getElementsByClassName("o_livechat_open")[0]//获取打开联系我们dom
    if (!liveChatDom) return

    const image = document.createElement( "img" ); //创建联系我们img
    image.src = listerner;
    contactUsDom.insertBefore(image, liveChatDom);
    image.style.width = "16px";
    image.style.margin = "12px 0 7px";
  }

  getContactUsFun = () => {
    this.setState({
      showContactUs: false
    })
    window.webfunnyEvent(5).trackEvent({
      yong_hu_id: Utils.getWfCookie("monitorCustomerKey"),
      dian_ji_xing_wei: "联系我们"
    })
  }
}
